<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search
      show-action
      placeholder="请输入搜索关键词"
      clearable
      v-model="useSearch"
    >
      <template #action>
        <div @click="search(useSearch)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="historyList.length > 0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16" @click="clearHistory" />
      </div>
      <div class="list">
        <div
          v-for="item in historyList"
          :key="item"
          class="list-item"
          @click="search(item)"
        >
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { setHistoryList, getHistoryList } from "@/utils/storage.js";
export default {
  name: "SearchIndex",
  data() {
    return {
      historyList: getHistoryList(),
      useSearch: "",
    };
  },
  methods: {
    // 获取搜索历史
    search(value) {
      if (value) {
        const history = this.historyList.indexOf(value);
        if (history === -1) {
          this.historyList.unshift(value);
          this.useSearch = "";
          this.$router.push(`/searchlist/?goodsName=${value}`);
          setHistoryList(this.historyList);
        } else {
          this.historyList.splice(history, 1);
          this.historyList.unshift(value);
          this.useSearch = "";
          // this.$router.push("/searchlist");
          this.$router.push(`/searchlist/?goodsName=${value}`);
          setHistoryList(this.historyList);
        }
      } else {
        Toast("请输入搜索关键词");
      }
    },
    clearHistory() {
      this.historyList = [];
      setHistoryList(this.historyList);
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>